    -ms-user-select: none;
    user-select: none;
}

.cursor {
    width: 5px;
    height: 2rem;
    background: white;
    margin-left: 10px;
}

.blinking {
    -webkit-animation: cursor-blink 1s forwards infinite;
            animation: cursor-blink 1s forwards infinite;
}

@-webkit-keyframes cursor-blink {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes cursor-blink {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

